.app-conatiner{
  max-width: 350px;
  margin: auto;
  border-radius: 30px;
  box-shadow: 2px 2px 5px 2px grey;
}
#root{
  padding-top: 20px;
}
.ant-layout-header{
  background: #fff;
  border-bottom: 1px solid #efefef;
}
.app-msg-wrapper{
  display: flex;
  flex-direction: column;
  height: 500px;
  overflow: auto;
  padding: 30px;
  background-color: #fff;
}
.app-msg-wrapper> .messages{
  margin: 8px 0;
}
.income{
  border: 1px solid #86a3ee;
  align-self: flex-start;
  border-radius: 25px 25px 25px 0;
  padding: 10px 20px;
}
.out_going{
  align-self: flex-end;
  background-color: #86a3ee;
    padding: 10px 15px;
    border-radius: 25px 25px 0 25px;
    color: #fff;
}
.app-wrapper{
  border-radius: 30px;
}
.ant-layout-footer{
  border-radius: 0 0 30px 30px;
  padding: 20px;
  background: #fff;
  border: 1px solid #efefef;
}
.ant-layout-header{
border-radius: 30px 30px 0 0;
}
.msg-typer{
  background-color: #86a3ee;
  color: #fff;
  border-radius: 20px;
  padding: 10px 30px;
}
#basic_message{
  background-color:#86a3ee;
  color: #fff;
}
#basic_message::placeholder{
  color: #fff;
  font-size: 14px;
  opacity: 0.5;
}
#basic>div{
  margin: auto;
}
.out_going{
  user-select: none;
}